<template>
    <v-container>
        <v-layout
                justify-center>
            <v-flex xs12>
                <v-card class="pl-4 pr-4 pt-2 pb-2">
                    <h1>Vue Input Groups</h1>
                    <section id="example" class="mb-5">
                        <h2 class="mt-2 mb-4">Example</h2>
                        <input-group v-model="example.value"
                                     :fields="example.fields"
                                     @validated="example.isValid = $event">
                        </input-group>
                        <span class="d-block text-xs-center ma-auto mb-3">Validation result is: {{ example.isValid }}</span>
                        <highlight-code class="mb-3" lang="javascript">
                            data() {
                                return {
                                    isValid: '',
                                    value: {
                                        fieldOne: "",
                                        fieldTwo: ""
                                    }
                                    fields: {
                                        fieldOne: {
                                            type: "text",
                                            placeholder: "Minimum 5 symbols",
                                            validation: {
                                                min: {
                                                    value: 5,
                                                    message: "Min is 5"
                                                }
                                            }
                                        },
                                        fieldTwo: {
                                            type: "password",
                                            placeholder: "Minimum 4 and max 20 symbols",
                                            validation: {
                                                min: {
                                                    value: 4,
                                                    message: "Min is 4"
                                                },
                                                max: {
                                                    value: 20,
                                                    message: "Max is 20"
                                                }
                                            }
                                        }
                                    }
                                }
                            },
                            components: {
                                InputGroup
                            }
                        </highlight-code>
                        <highlight-code lang="vue">
                           &lt;input-group v-model="value"
                                         @validated="isValid = $event"
                                         :fields="fields"&gt;
                            &lt;/input-group&gt;
                            Validation result is: &#123; &#123; isValid &#125; &#125;
                        </highlight-code>
                    </section>
                    <h1>Guide</h1>
                    <section class="mt-2" id="guide">
                        <h2 id="installation" class="mb-2">Installation</h2>
                        <highlight-code class="mt-2 mb-2">
                            npm install --save vue-input-groups
                        </highlight-code>
                        <h2 id="quick-start" class="mt-2">Quick start</h2>
                        Import package into your project:
                        <highlight-code class="mt-3 mb-3" lang="javascript">
                            import InputGroup from "vue-input-groups"
                        </highlight-code>
                        Then, add imported package into "components" and create a value container and fields description:
                        <highlight-code class="mt-3 mb-3" lang="javascript">
                            components: {
                                InputGroup
                            },
                            data() {
                                return {
                                    value: {
                                        name: "",
                                        password: ""
                                    },
                                    fields: {
                                        //Keys in fields description have to be
                                        //named as well as keys in value container
                                        name: {
                                            //Field type (list of currently supported types is in the API section)
                                            type: "text",
                                            //Placeholder
                                            placeholder: "Enter your name"
                                        },
                                        password: {
                                            type: "password",
                                            placeholder: "Enter your password"
                                        }
                                    }
                                }
                            }
                        </highlight-code>
                        Simple usage example:
                        <highlight-code class="mt-3 mb-3" lang="vue">
                           &lt;input-group v-model="value"
                                         :fields="fields"&gt;
                            &lt;/input-group&gt;
                        </highlight-code>
                        <h2 id="validation">Validation</h2>
                        To enable validation, add this key in field description and describe the rules (<a href="#">full list of rules</a>):
                        <highlight-code class="mt-3 mb-3" lang="javascript">
                            data() {
                                return {
                                    //...
                                    fields: {
                                        someField: {
                                            type: "text",
                                            validation: {
                                                required: "This field is required",
                                                min: {
                                                    value: 5,
                                                    message: "Min is 5"
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                        </highlight-code>
                        <h2 id="validation-result">Getting validation result</h2>
                        Event "Validated" returns result of input group validation. You can assign this value to some your variable:
                        <highlight-code class="mt-3 mb-3" lang="javascript">
                            data() {
                                return {
                                    //...
                                    isValid: '',
                                    //...
                                }
                            },
                        </highlight-code>
                        <highlight-code class="mb-3" lang="vue">
                           &lt;input-group @validated="isValid = $event"&gt;
                            &lt;/input-group&gt;
                        </highlight-code>
                        <h2 id="mobile" class="mt-3">Display on mobile</h2>
                        set up breakpoint to switch display of warnings on mobile:
                        <highlight-code class="mt-3 mb-3" lang="vue">
                           //show mobile version with window width 567 (default is 400)
                            &lt;input-group :breakpoint="567"&gt;
                            &lt;/input-group&gt;
                        </highlight-code>
                        <h2 id="errors-display">Changing the errors display position</h2>
                        To change the errors position create key "config" in field description. Then, specify warnY (Y-position)(top, center, bottom) or warnX (X-position)(left, right):
                        <highlight-code class="mt-3 mb-3" lang="javascript">
                            data() {
                                return {
                                    //...
                                    fields: {
                                        someField: {
                                            type: "text",
                                            config: {
                                                warnY: "top",
                                                warnX: "left"
                                            }
                                        }
                                    }
                                }
                            }
                        </highlight-code>
                        <h2 id="delayed-display">Delayed displaying errors</h2>
                        If you want errors to be shown only after some event (by example, after user submit a form), pass false in argument "showWarnings" and after event occurrence, change this value to true:
                        <highlight-code class="mt-3 mb-3" lang="javascript">
                            data() {
                                return {
                                    //...
                                    showWarnings: false,
                                }
                            }
                        </highlight-code>
                        <highlight-code class="mt-3 mb-3" lang="vue">
                           &lt;input-group :show-warnings="showWarnings"&gt;
                            &lt;/input-group&gt;
                            &lt;button @click="showWarnings=true"&gt;Show warnings&lt;/button&gt;
                        </highlight-code>
                        <h2 id="custom-warnings">Custom warnings</h2>
                        If you wanna to display you custom messages, just add an array of these messages in field description:
                        <highlight-code class="mt-3 mb-3" lang="javascript">
                            data() {
                                return {
                                    //...
                                    fields: {
                                        someField: {
                                            type: "text",
                                            messages: [
                                                "Wrong password"
                                            ]
                                        }
                                    }
                                }
                            }
                        </highlight-code>
                        <h2 id="multiselect">Using select and multiselect</h2>
                        Select field have been made with <a href="https://www.npmjs.com/package/vue-multiselect">vue-multiselect</a>. Here is the example of using this package in vue-input-group:
                        <highlight-code class="mt-3 mb-3" lang="javascript">
                            data() {
                                return {
                                    //...
                                    fields: {
                                        someField: {
                                            type: "select",
                                            placeholder: "Select something",
                                            //Objects in options array have to contain keys 'label' for display this option and 'id' for tracking
                                            options: [
                                                {
                                                    label: "Item 1",
                                                    id: 1
                                                }
                                            ],
                                            //In config you can specify properties for vue-multiselect
                                            //Check out its readme to know about all of its properties
                                            config: {
                                                multiple: true,
                                                selectLabel: "Select this",
                                                selectedLabel: "It is selected"
                                            },
                                            validation: {
                                                required: "Choose at least 1",
                                                max: {
                                                    value: 10,
                                                    message: "Maximum 10 items"
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                        </highlight-code>
                        <h2 id="tel-input">Using phone number field</h2>
                        Phone number field have been made with <a href="https://www.npmjs.com/package/vue-multiselect">vue-multiselect</a>. Here is the example of using this package in vue-input-group:
                        <highlight-code class="mt-3 mb-3" lang="javascript">
                            data() {
                                return {
                                    //...
                                    fields: {
                                        someField: {
                                            type: "tel",
                                            placeholder: "Enter your phone number",
                                            //In config you can specify properties for vue-tel-input
                                            //Check out its readme to know about all of its properties
                                            config: {
                                                defaultCountry: 'ru'
                                            },
                                            validation: {
                                                isPhone: "Incorrect phone number"
                                            }
                                        }
                                    }
                                }
                            }
                        </highlight-code>
                    </section>
                    <h1 id="api">API</h1>
                    <section>
                        <h2 id="properties">Properties</h2>
                        <v-data-table
                                :items="properties"
                                class="elevation-1 mt-3 mb-3"
                                hide-actions
                                :headers="propsHeaders">
                            <template slot="items" slot-scope="props">
                                <td>{{ props.item.title }}</td>
                                <td class="text-xs-right">{{ props.item.type }}</td>
                                <td class="text-xs-right">{{ props.item.default }}</td>
                                <td class="text-xs-right">{{ props.item.description }}</td>
                            </template>
                        </v-data-table>
                        <h2 id="types">Supported field types</h2>
                        <v-data-table
                                :items="types"
                                class="elevation-1 mt-3 mb-3"
                                hide-actions
                                hide-headers>
                            <template slot="items" slot-scope="props">
                                <td>{{ props.item.type }}</td>
                                <td class="text-xs-right" v-html="props.item.description"></td>
                            </template>
                        </v-data-table>
                        <h2 id="rules">Validation rules</h2>
                        <v-data-table
                                :items="rules"
                                class="elevation-1 mt-3 mb-3"
                                hide-actions
                                hide-headers>
                            <template slot="items" slot-scope="props">
                                <td>{{ props.item.rule }}</td>
                                <td class="text-xs-right" v-html="props.item.type"></td>
                                <td class="text-xs-right" v-html="props.item.description"></td>
                            </template>
                        </v-data-table>
                    </section>
                </v-card>
            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
    import InputGroup from 'vue-input-groups'
    import "vue-input-groups/dist/VueInputGroups.css"

    export default {
        data: () => ({
            rules: [
                {
                    rule: "min",
                    description: "Minimal value",
                    type: '{ value: Number, message: String }'
                },
                {
                    rule: "max",
                    description: "Maximal value",
                    type: '{ value: Number, message: String }'
                },
                {
                    rule: "isEmail",
                    description: "Set up the message which will be shown when a value is not an email address (only for email field)",
                    type: 'String'
                },
                {
                    rule: "isPhone",
                    description: "Set up the message which will be shown when a value is not a phone number (only for tel field)",
                    type: 'String'
                },
                {
                    rule: "required",
                    description: "Make a field required and set message which will be shown if field is empty",
                    type: 'String'
                },
            ],
            types: [
                {
                    type: "text",
                    description: "Text field"
                },
                {
                    type: "number",
                    description: "Number field"
                },
                {
                    type: "email",
                    description: "Email field"
                },
                {
                    type: "password",
                    description: "Password field"
                },
                {
                    type: "select",
                    description: "Select field (made with <a target='_blank' href='https://www.npmjs.com/package/vue-multiselect'>Vue-multiselect</a>)"
                },
                {
                    type: "tel",
                    description: "Phone number field (made with <a target='_blank' href='https://www.npmjs.com/package/vue-tel-input'>Vue-tel-input</a>)"
                },
            ],
            propsHeaders: [
                {
                    text: 'Name',
                    align: 'left',
                    sortable: false,
                    value: 'title'
                },
                {
                    text: 'Type',
                    align: 'right',
                    sortable: false,
                    value: 'type'
                },
                {
                    text: 'Default',
                    align: 'right',
                    sortable: false,
                    value: 'default'
                },
                {
                    text: 'Description',
                    align: 'right',
                    sortable: false,
                    value: 'description'
                },
            ],
            properties: [
                {
                    title: 'fields',
                    type: 'Object',
                    default: {},
                    description: "Fields descriptions"
                },
                {
                    title: 'breakpoint',
                    type: 'Number',
                    default: 400,
                    description: "Width of the window, after reaching which the mobile display will turn on"
                },
                {
                    title: 'show-warnings',
                    type: 'Boolean',
                    default: "true",
                    description: "Enable / Disable Error Display"
                },
            ],
            example: {
                isValid: '',
                value: {
                    fieldOne: "",
                    fieldTwo: ""
                },
                fields: {
                    fieldOne: {
                        type: "text",
                        placeholder: "Minimum 5 symbols",
                        validation: {
                            min: {
                                value: 5,
                                message: "Min is 5"
                            }
                        }
                    },
                    fieldTwo: {
                        type: "password",
                        placeholder: "Minimum 4 and max 20 symbols",
                        validation: {
                            min: {
                                value: 4,
                                message: "Min is 4"
                            },
                            max: {
                                value: 20,
                                message: "Max is 20"
                            }
                        }
                    }
                }
            }
        }),
        components: {
            InputGroup
        }
    }
</script>

<style lang="sass">
    .hljs
        font-size: 14px
    .input-group
        .input-group__label
            max-width: 400px
            margin-left: auto
            margin-right: auto
            margin-bottom: 15px
            .input-group__error
                font-size: 14px
</style>
